body {
  /* background-color: #000; */
  background-image: url("./assets/home-page.png");
  /* background-repeat: no-repeat, repeat; */
  background-size: 100%;
}

.App {
  /* background-color: #000; */
  min-height: 100vh;
  margin: 0 auto;
  max-width: 1600px;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.amazon-image {
  width: 605px;
  height: 339px;
}

.simple-text {
  color: #C1C1C1;
  width: 606px;
}

.image-card {
  display: flex;
  width: 605px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.image-card .image-body {
  padding-left: 35px;
  color: #fff;
  background: rgba(0, 0, 0, 0.08);
  box-shadow: 3px 4px 4px rgb(0 0 0 / 60%);
  padding-right: 35px;
}

.image-card .text-secondary {
  font-weight: 800;
  font-size: 18px;
  line-height: 22px;
  color: #E7E7E7;
}

.image-card .text-secondary {
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
  color: #D6D6D6;
}

.card-post-deliverd-title-1 {
  padding: 35px 55px 32px 55px;
  text-align: center;
  font-family: 'Inter' !important;
  font-style: normal;
  line-height: 25px !important;
}

.card-post-deliverd-title-2 {
  padding-left: 55px;
  padding-right: 55px;
  font-family: 'Inter' !important;
  font-style: normal !important;
  font-size: 15px !important;
  line-height: 18px !important;
  text-align: center;
}

footer ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

footer li {
  margin: 1em;
  margin-left: 3em;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #E7E7E7;
}

footer li .right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.arrow {
  border: solid #505050;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  margin-right: 10px;
}

.copyright{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  color: #979797;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.MuiPaper-root {
  background-color: black !important;
}